.order-layout {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin: 24px;

  .search-add-bar {
    display: flex;
    justify-items: center;
    margin-bottom: 15px;
    height: 50px;
    .search-div {
      display: flex;
      flex-grow: 1;
      align-items: center;
      .search-input {
        border-radius: 4px;
        width: 200px;
        @include font-12;
        padding: 10px 16px;
        font-family: $font-regular, sans-serif;
      }
    }
  }
}
.orders {
  display: flex;
  flex-direction: column;
  .orders-form {
    background-color: $color--light;
    display: grid;
    flex-direction: column;
    margin: 8px 50px;
    padding: 8px;
    @include font-14;
    @include shadow;
    .orders-row-id_status {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      .orders-status {
        width: fit-content;
        align-self: flex-end;
      }
    }
    .orders-row-customer {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      .orders-pnum {
        width: fit-content;
        align-self: flex-end;
      }
    }
    .orders-row-total {
      width: fit-content;
      justify-self: flex-end;
    }
  }
}
.font_italic {
  font-style: italic;
}
.bold {
  font-weight: bold;
}
.product-row {
  display: flex;
  flex-direction: row;
  border-bottom: 1px solid $color--grey;
  @include font-12;
  .product-property {
    margin: 4px;
    width: 250px;
  }
  .product-value {
    margin: 4px;
    flex-grow: 1;
    text-align: right;
  }
}
.orders-row {
  display: flex;
  justify-content: flex-end;
  .expand-button {
    width: 100px;
  }
  &-address {
    color: red;
  }
}
.text-blue {
  color: blue;
}
.complete-order-btn {
  background-color: $color--complete-button;
}

.cancel-order-btn {
  background-color: $color--delete-button;
}
.cancel-order-btn:hover {
  background-color: $color--black;
}
.filter-order-button-false {
  @include font-14;
  border-radius: 20px;
  height: fit-content;
}
.filter-order-button-true {
  @include font-14;
  border-radius: 20px;
  height: fit-content;
  background-color: $color--filterd;
  color: $color--black;
  border-color: $color--grey;
}
.filter-div {
  display: flex;
  align-items: center;
  justify-self: center;
  margin-left: 20px;
}
